import React from "react";

import { Component } from "react";

import Utils from "../helpers/utils";

import sha256 from "crypto-js/sha256";
import Base64 from "crypto-js/enc-base64";

const COLORS = [
  "red",
  "blue",
  "green",
  "orange",
  "purple",
  "yellow",
  "black",
  "gray",
];
const SHAPES = [3, 4, 5, 6, 7, 8, 9, 100];
const Y_MAX = 700;
const X_MAX = 700;
const Y_OFFSET = 0;
const X_OFFSET = 0;

class KaleidoscopeColoring extends Component {
  constructor(props) {
    super(props);
    this.STARTING_STATE = {
      value: "ENDLESSWIPSS",
    };
    this.state = this.STARTING_STATE;

    this.canvasRef = React.createRef();
  }

  getFieldUpdateFunction(fieldName) {
    return function (e) {
      let stateUpdate = {};
      if (e.target.type === "checkbox") {
        stateUpdate[fieldName] = e.target.checked;
      } else {
        stateUpdate[fieldName] = e.target.value;
      }
      this.setState(stateUpdate);
    }.bind(this);
  }

  drawShape(ctx, color, distance, sides, originX, originY, outline) {
    ctx.beginPath();
    for (let s = 0; s < sides; s++) {
      let angle = (2 * Math.PI) / sides;
      let x = distance * Math.sin(s * angle);
      let y = distance * Math.cos(s * angle);
      ctx.lineTo(x + originX, y + originY);
    }
    ctx.fillStyle = color;
    ctx.fill();

    if (outline) {
      ctx.beginPath();
      for (let s = 0; s < sides; s++) {
        let angle = (2 * Math.PI) / sides;
        let x = (distance - 1) * Math.sin(s * angle);
        let y = (distance - 1) * Math.cos(s * angle);
        ctx.lineTo(x + originX, y + originY);
      }
      ctx.fillStyle = "white";
      ctx.fill();
    }
  }

  clipShape(ctx, distance, sides, originX, originY) {
    ctx.beginPath();
    for (let s = 0; s < sides; s++) {
      let angle = (2 * Math.PI) / sides;
      let x = distance * Math.sin(s * angle);
      let y = distance * Math.cos(s * angle);
      ctx.lineTo(x + originX, y + originY);
    }
    ctx.clip();
  }

  async getTriangle(
    canvas,
    hexString,
    size,
    particleRelativeSize,
    particleCount,
    outline
  ) {
    let ctx = canvas.getContext("2d");

    // Debug: Clear the canvas
    //ctx.clearRect(0, 0, canvas.width, canvas.height);

    ctx.save();
    //this.clipShape(ctx, size, 3, X_MAX / 2, Y_MAX / 2);
    this.drawShape(ctx, "red", size, 3, X_MAX / 2, Y_MAX / 2);
    ctx.globalCompositeOperation = "source-atop";

    for (let shapeNumber = 0; shapeNumber < particleCount; shapeNumber++) {
      const USED_BITS = 16;
      const shift = 1 * shapeNumber; // I stopped caring that every bit was unique

      let hash = Utils.hexToBinary(hexString);
      let color = COLORS[parseInt(hash.substring(shift + 0, shift + 3), 2)];
      let shape = SHAPES[parseInt(hash.substring(shift + 4, shift + 7), 2)];

      let xLoc =
        (Math.floor(parseInt(hash.substring(shift + 8, shift + 18), 2)) /
          Math.pow(2, 10)) *
          size *
          2 +
        X_MAX / 2 -
        size;
      let yLoc =
        (Math.floor(parseInt(hash.substring(shift + 18, shift + 28), 2)) /
          Math.pow(2, 10)) *
          size *
          2 +
        Y_MAX / 2 -
        size;
      /*
      let xLoc =
        (Math.floor(parseInt(hash.substring(shift + 8, shift + 11), 2)) *
          X_MAX) /
        7;
      let yLoc =
        (Math.floor(parseInt(hash.substring(shift + 12, shift + 15), 2)) *
          Y_MAX) /
        7;
        */

      this.drawShape(
        ctx,
        outline ? "black" : color,
        size * particleRelativeSize,
        shape,
        xLoc,
        yLoc,
        outline
      );
    }
    ctx.restore();

    // Save tri as image
    let imgDataUrl = canvas.toDataURL("image/webp", 1);
    let img = new Image();
    await new Promise(r => (img.onload = r), (img.src = imgDataUrl));

    // Clear the canvas
    //ctx.clearRect(0, 0, canvas.width, canvas.height);
    return img;
  }

  drawTriangle(ctx, img, x, y, angle, flip) {
    ctx.save();

    // Mirror if requested
    if (flip) {
      ctx.scale(1, -1);
      ctx.translate(0, -Y_MAX);
      y = -y;
    }

    // Draw at location
    ctx.translate(x, y);

    // Rotate at origin
    let mod = -2;
    ctx.translate(-X_MAX / mod, -Y_MAX / mod);
    ctx.rotate(angle);
    ctx.translate(X_MAX / mod, Y_MAX / mod);

    // Do the drawing!
    ctx.drawImage(img, 0, 0);

    // Leave everything like we found it
    ctx.restore();
  }

  async onMountOrUpdate() {
    //console.log("UPDATED!");
    let canvas = this.canvasRef.current;
    if (canvas && canvas.getContext) {
      const size = 60; //X_MAX / 4;
      let base64String = Base64.stringify(sha256(this.state.value)); //(Math.random() + 1).toString(36).substring(7);
      console.log(base64String);

      let ctx = canvas.getContext("2d");

      // Clear the canvas
      ctx.clearRect(0, 0, canvas.width, canvas.height);

      let tri = await this.getTriangle(
        canvas,
        Utils.base64ToHex(base64String),
        size,
        0.5,
        128,
        true
      );
      ///*
      ctx.translate(X_OFFSET, Y_OFFSET);

      const P2 = 2 * Math.PI;

      // https://docs.google.com/spreadsheets/d/1MDUV0UjwIbVSi0c-AKLsLJ3QY4aaoHlQI0AUA2YqhhU/edit#gid=0
      const A = Math.cos(1.0472) * size;
      const B = Math.sin(1.0472) * size;
      const C = A * 3; // C

      // Zoom out/in?
      //ctx.scale(1.5, 1.5);

      // Level 0
      this.drawTriangle(ctx, tri, 0, 0, P2 * (0 / 3), false);

      // Level 1
      this.drawTriangle(ctx, tri, 0, -C + A, P2 * (0 / 3), true);
      this.drawTriangle(ctx, tri, B, A, P2 * (1 / 3), true);
      this.drawTriangle(ctx, tri, -B, A, P2 * (2 / 3), true);

      // Level 3
      this.drawTriangle(ctx, tri, B, -C, P2 * (1 / 3), false);
      this.drawTriangle(ctx, tri, -B, -C, P2 * (2 / 3), false);

      this.drawTriangle(ctx, tri, -B * 2, 0, P2 * (1 / 3), false);
      this.drawTriangle(ctx, tri, B * 2, 0, P2 * (2 / 3), false);

      this.drawTriangle(ctx, tri, -B, C, P2 * (2 / 3), false);
      this.drawTriangle(ctx, tri, B, C, P2 * (1 / 3), false);

      // Level 4
      this.drawTriangle(ctx, tri, B, -C * 2 + A, P2 * (1 / 3), true);
      this.drawTriangle(ctx, tri, -B, -C * 2 + A, P2 * (2 / 3), true);

      this.drawTriangle(ctx, tri, B * 2, -C + A, P2 * (2 / 3), true);
      this.drawTriangle(ctx, tri, -B * 2, -C + A, P2 * (1 / 3), true);

      this.drawTriangle(ctx, tri, B * 3, A, P2 * (0 / 3), true);
      this.drawTriangle(ctx, tri, -B * 3, A, P2 * (0 / 3), true);

      this.drawTriangle(ctx, tri, -B * 2, C + A, P2 * (1 / 3), true);
      this.drawTriangle(ctx, tri, 0, C + A, P2 * (0 / 3), true);
      this.drawTriangle(ctx, tri, B * 2, C + A, P2 * (2 / 3), true);

      // Level 5
      this.drawTriangle(ctx, tri, -B * 2, -C * 2, P2 * (1 / 3), false);
      this.drawTriangle(ctx, tri, 0, -C * 2, P2 * (0 / 3), false);
      this.drawTriangle(ctx, tri, B * 2, -C * 2, P2 * (2 / 3), false);

      this.drawTriangle(ctx, tri, -B * 3, -C, P2 * (0 / 3), false);
      this.drawTriangle(ctx, tri, B * 3, -C, P2 * (0 / 3), false);

      this.drawTriangle(ctx, tri, -B * 4, 0, P2 * (2 / 3), false);
      this.drawTriangle(ctx, tri, B * 4, 0, P2 * (1 / 3), false);

      this.drawTriangle(ctx, tri, -B * 3, C, P2 * (0 / 3), false);
      this.drawTriangle(ctx, tri, B * 3, C, P2 * (0 / 3), false);

      this.drawTriangle(ctx, tri, -B * 2, C * 2, P2 * (1 / 3), false);
      this.drawTriangle(ctx, tri, 0, C * 2, P2 * (0 / 3), false);
      this.drawTriangle(ctx, tri, B * 2, C * 2, P2 * (2 / 3), false);

      // Level 6
      this.drawTriangle(ctx, tri, -B * 2, -C * 3 + A, P2 * (1 / 3), true);
      this.drawTriangle(ctx, tri, 0, -C * 3 + A, P2 * (0 / 3), true);
      this.drawTriangle(ctx, tri, B * 2, -C * 3 + A, P2 * (2 / 3), true);

      this.drawTriangle(ctx, tri, -B * 3, -C * 2 + A, P2 * (0 / 3), true);
      this.drawTriangle(ctx, tri, B * 3, -C * 2 + A, P2 * (0 / 3), true);

      this.drawTriangle(ctx, tri, -B * 4, -C + A, P2 * (2 / 3), true);
      this.drawTriangle(ctx, tri, B * 4, -C + A, P2 * (1 / 3), true);

      this.drawTriangle(ctx, tri, -B * 5, A, P2 * (1 / 3), true);
      this.drawTriangle(ctx, tri, B * 5, A, P2 * (2 / 3), true);

      this.drawTriangle(ctx, tri, -B * 4, C + A, P2 * (2 / 3), true);
      this.drawTriangle(ctx, tri, B * 4, C + A, P2 * (1 / 3), true);

      this.drawTriangle(ctx, tri, -B * 3, C * 2 + A, P2 * (0 / 3), true);
      this.drawTriangle(ctx, tri, -B, C * 2 + A, P2 * (2 / 3), true);
      this.drawTriangle(ctx, tri, B, C * 2 + A, P2 * (1 / 3), true);
      this.drawTriangle(ctx, tri, B * 3, C * 2 + A, P2 * (0 / 3), true);

      // TODO: offset back?
      //*/
    }
  }

  async componentDidMount() {
    this.onMountOrUpdate();
  }

  async componentDidUpdate() {
    this.onMountOrUpdate();
  }

  render() {
    return (
      <div style={{ textAlign: "center" }}>
        <input
          type="string"
          onChange={this.getFieldUpdateFunction("value")}
          placeholder={"Pattern Code"}
        ></input>
        <div
          style={{
            marginLeft: this.props.padding,
          }}
        >
          <div style={{ display: "flex", margin: "0 auto", width: "94%" }}>
            <canvas
              ref={this.canvasRef}
              width={X_MAX}
              height={Y_MAX}
              style={{
                //border: "black",
                //borderStyle: "solid",
                borderWidth: "1px",
                borderRadius: "3px",
                marginRight: "4px",
              }}
            ></canvas>
          </div>
        </div>
      </div>
    );
  }
}
export default KaleidoscopeColoring;
